import React,{Component} from 'react';
//postComment 
import {getOrderList,postComment} from '../../../fetch/orderList';
import OrderListComponent from "../../../components/OrderListComponent/index";
export default class OrderList extends Component{
    constructor(){
        super();
        this.state={
            data:[]
        }
    }
    render(){
        return (
            <div>
                {this.state.data.length?<OrderListComponent data={this.state.data} commitComment={this.commitComment.bind(this)}/>:<div></div>}
            </div>
        )
    }
    //
    commitComment(id,comment,callback){   //idcallback 
      console.log(id,comment);

        postComment({id,comment}).then(res=>res.json()).then(data=>{
            callback();//
        });
    }
    componentDidMount(){
        getOrderList(this.props.username).then(res=>res.json()).then(data=>{  //jsonresfetch

            this.setState({
                data
            })
        });
    }
}




