import React,{Component} from 'react';
import './inex.less'
export default class UserInfo extends Component{
    render(){
        return (
            <div className="user-info">
                <span>{this.props.userInfo.username}</span>
                <span>{this.props.userInfo.cityName}</span>
            </div>
        )
    }
    //
}



